window.addEventListener('DOMContentLoaded', () => {
    gsap.registerPlugin(ScrollTrigger);
    if(document.querySelector(".index-page")){
        indexPageAnim()

    }

    // gradientAnim()
})

function indexPageAnim() {
    gsap.from(".main-slider",{
        opacity: 0,
        xPercent: 100,
        duration: 2,
        ease: "power4"
    })

    productCardAnim()
    productCardAnim2()

    let workCards = document.querySelectorAll(".work-card")
    workCards.forEach(card => {
        cardAnimation(card)
    })


    let titles = document.querySelectorAll(".text52")
    titles.forEach(title => {
        titleAnimation(title)
    })
}


function productCardAnim() {
    let productCards = document.querySelectorAll(".product-card2")

    productCards.forEach(card => {
        let title = card.querySelector(".product-card2__top")
        let img = card.querySelector(".product-card2__img")

        const tl = gsap.timeline({
            scrollTrigger: {
                trigger: card,
                start: 'top bottom',
            }
        })

        tl.from(title, {
            x: 100,
            opacity: 0,
            duration: 2,
            ease: "power4"
        }, 0.4)
        .from(img, {
            x: -100,
            opacity: 0,
            duration: 2,
            ease: "power4"
        },'<')
    })
}

function productCardAnim2() {
    let productCards = document.querySelectorAll(".product-card3")

    productCards.forEach(card => {
        let img = card.querySelector(".product-card3__img")
        let info = card.querySelector(".product-card3__info")
        let props = card.querySelector(".product-card3__props")

        const tl = gsap.timeline({
            scrollTrigger: {
                trigger: card,
                start: 'top bottom',
            }
        })

        tl.from(img, {
            opacity: 0,
            x: -100,
            duration: 2,
            ease: "power4"
        })
        .from(info, {
            x: 100,
            opacity: 0,
            duration: 2,
            ease: "power4"
        },"+0.4")
        .from(props, {
            x: -100,
            opacity: 0,
            duration: 2,
            ease: "power4"
        },"+0.4")
    })
}


function cardAnimation(card) {
    gsap.from(card, {
        // yPercent: 40,
        opacity: 0,
        duration: 3,
        scrollTrigger: {
            trigger: card,
            start: 'top bottom',
            toggleActions: 'play none none reverse'
        }
    })
}

function titleAnimation(title) {
    gsap.from(title, {
        x: -100,
        opacity: 0,
        duration: 1,
        scrollTrigger: {
            trigger: title,
            start: 'top bottom',
            toggleActions: 'play none none reverse'
        }
    })
}
